import React, { useState } from 'react';
import axios from 'axios';
import styled from 'styled-components';
import * as XLSX from 'xlsx';

const Container = styled.div`
  margin: 20px;
  font-family: 'Noto Sans KR', sans-serif;
  text-align: center;
`;

const Title = styled.h1`
  font-size: 24px;
  margin-bottom: 20px;
`;

const FormContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
`;

const Label = styled.label`
  font-weight: bold;
  margin-bottom: 10px;
`;

const Input = styled.input`
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 300px;
  margin-bottom: 10px;
`;

const Button = styled.button`
  padding: 10px 20px;
  background-color: #000;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: 16px;
  margin-top: 10px;

  &:hover {
    background-color: #555;
  }
`;

const ResultContainer = styled.div`
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;
  text-align: left;
`;

const ResultTitle = styled.h2`
  font-size: 18px;
  margin-bottom: 10px;
`;

const ResultItem = styled.div`
  margin-bottom: 10px;
`;

const Warning = styled.div`
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #ffcc00;
  background-color: #fff8e1;
  color: #e65100;
  border-radius: 5px;
  font-size: 0.9em;
  line-height: 1.5;
`;

const BusinessNumberLookup = () => {
  const [bNo, setBNo] = useState('');
  const [input2, setInput2] = useState('');
  const [result, setResult] = useState(null);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);
  const [bulkResults, setBulkResults] = useState([]);

  const handleSearch = async () => {
    const cleanedBNo = bNo.replace(/-/g, '');

    if (cleanedBNo.trim() === '') {
      alert(" .");
      return;
    }

    const data = {
      b_no: [cleanedBNo]
    };

    try {
      setLoading(true);
      setError(null);

      const response = await axios.post(
        'https://api.odcloud.kr/api/nts-businessman/v1/status?serviceKey=CADGVCpJ6S3ugec34rtjEC4Fq1h0t0sbaD%2BchVRlpPGrKdOCDgyGmI0WnIpPQf4d7a4EPfo8FXmTmJqWxPrqrQ%3D%3D&',
        data,
        {
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json'
          }
        }
      );

      console.log('API :', response.data);

      if (response.data.status_code !== "OK" || response.data.match_cnt === 0) {
        setError({ msg: "   ." });
      } else {
        const businessData = response.data.data[0];
        if (businessData.b_stt === "   .") {
          setError({ msg: businessData.b_stt });
          setResult(null);
        } else {
          const formattedResult = {
            b_no: formatBusinessNumber(businessData.b_no),
            b_stt: businessData.b_stt,
            tax_type: businessData.tax_type
          };
          setResult(formattedResult);
          setError(null);
        }
      }
    } catch (err) {
      setError(err.response ? err.response.data : { msg: "API    ." });
      setResult(null);
    } finally {
      setLoading(false);
    }
  };

  const formatBusinessNumber = (bNo) => {
    return bNo.replace(/(\d{3})(\d{2})(\d{5})/, '$1-$2-$3');
  };

  const handleBNoChange = (e) => {
    const formattedBNo = e.target.value.replace(/[^0-9-]/g, '');
    setBNo(formattedBNo);
  };

  const handleInput2Change = (e) => {
    setInput2(e.target.value);
  };

  const handleFileUpload = (e) => {
    const file = e.target.files[0];

    if (file) {
      const reader = new FileReader();

      reader.onload = async (event) => {
        const data = new Uint8Array(event.target.result);
        const workbook = XLSX.read(data, { type: 'array' });
        const sheetName = workbook.SheetNames[0];
        const sheet = workbook.Sheets[sheetName];
        const rows = XLSX.utils.sheet_to_json(sheet, { header: 1 });

        const businessNumbers = rows.map(row => {
          if (typeof row[0] === 'string') {
            return row[0].replace(/-/g, ''); //  
          }
          return null;
        }).filter(bNo => bNo !== null);

        console.log('  :', businessNumbers);

        try {
          setLoading(true);
          setError(null);

          const response = await axios.post(
            'https://api.odcloud.kr/api/nts-businessman/v1/status?serviceKey=CADGVCpJ6S3ugec34rtjEC4Fq1h0t0sbaD%2BchVRlpPGrKdOCDgyGmI0WnIpPQf4d7a4EPfo8FXmTmJqWxPrqrQ%3D%3D&',
            { b_no: businessNumbers },
            {
              headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
              }
            }
          );

          console.log('API :', response.data);

          const formattedResults = response.data.data.map(item => ({
            b_no: formatBusinessNumber(item.b_no),
            b_stt: item.b_stt,
            tax_type: item.tax_type
          }));

          const validResults = formattedResults.filter(item => item.b_stt !== "   .");

          setBulkResults(validResults);
          if (validResults.length === 0) {
            setError({ msg: "    ." });
          }
        } catch (err) {
          setError(err.response ? err.response.data : { msg: "API    ." });
        } finally {
          setLoading(false);
        }
      };

      reader.readAsArrayBuffer(file);
    }
  };

  const handleExcelDownload = () => {
    const wsData = bulkResults.map(result => [
      result.b_no,
      result.b_stt,
      result.tax_type
    ]);

    const ws = XLSX.utils.aoa_to_sheet([['', ' ', ' '], ...wsData]);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, '  ');
    XLSX.writeFile(wb, '__.xlsx');
  };

  const downloadExcelTemplate = () => {
    const templateData = [
      [''],
      ['123-45-67890'],
      ['234-56-78901'],
      ['345-67-89012']
      //       .
    ];

    const ws = XLSX.utils.aoa_to_sheet(templateData);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, ' ');
    XLSX.writeFile(wb, '_.xlsx');
  };

  return (
    <Container>
      <Title> </Title>
      <FormContainer>
        <Label htmlFor="bNo">:</Label>
        <Input
          type="text"
          id="bNo"
          value={bNo}
          onChange={handleBNoChange}
          placeholder=" "
        />
        <Button onClick={handleSearch}></Button>
      </FormContainer>

      <Button onClick={downloadExcelTemplate}>  </Button>

      {loading && <p> ...</p>}

      {error && (
        <ResultContainer>
          <ResultTitle>:</ResultTitle>
          <div style={{ color: 'red' }}>
            <pre>{JSON.stringify(error, null, 2)}</pre>
          </div>
        </ResultContainer>
      )}

      {result && (
        <ResultContainer>
          <ResultTitle> :</ResultTitle>
          <ResultItem>
            <Label>:</Label>
            <span>{result.b_no}</span>
          </ResultItem>
          <ResultItem>
            <Label> :</Label>
            <span>{result.b_stt}</span>
          </ResultItem>
          <ResultItem>
            <Label> :</Label>
            <span>{result.tax_type}</span>
          </ResultItem>

          {result.b_stt === '' && (
            <div style={{ marginTop: '20px' }}>
              <Label htmlFor="input2"> :</Label>
              <Input
                type="text"
                id="input2"
                value={input2}
                onChange={handleInput2Change}
                placeholder="  "
              />
            </div>
          )}
        </ResultContainer>
      )}

      <FormContainer>
        <Label htmlFor="fileUpload">  :</Label>
        <Input
          type="file"
          id="fileUpload"
          accept=".xlsx, .xls"
          onChange={handleFileUpload}
        />
      </FormContainer>

      {bulkResults.length > 0 && (
        <ResultContainer>
          <ResultTitle>  :</ResultTitle>
          {bulkResults.map((res, index) => (
            <ResultItem key={index}>
              <Label>:</Label>
              <span>{res.b_no}</span>
              <br />
              <Label> :</Label>
              <span>{res.b_stt}</span>
              <br />
              <Label> :</Label>
              <span>{res.tax_type}</span>
            </ResultItem>
          ))}
        </ResultContainer>
      )}

      <Warning>
                    ,    .<br />
          (,  )   .
      </Warning>

      <Button onClick={handleExcelDownload}> </Button>
    </Container>
  );
};

export default BusinessNumberLookup;
